import React from 'react';
import logo from './logo.svg';
import './App.css';
import { StreetView } from 'react-google-map-street-view';

import Slide from './Slide';
import Home from './Home';
import Travel from './Travel';

const getSlide = (index, onClick, location = '', setLocation) => {
  console.log(location);
  const slides = [
    <Home onStart={onClick} />,
    <Slide
      key="1"
      onNext={onClick}
      question="Recordes amb afecte els llocs on vas créixer?"
    ></Slide>,
    <Slide
      key="2"
      onNext={onClick}
      question="Podries pensar en un lloc de la teva infància que recordis amb especial estima?"
    ></Slide>,
    <Slide
      key="3"
      onNext={onClick}
      question="Com descriuries la relació que tens amb aquest lloc en concret i perquè creus que la tens?"
    ></Slide>,
    <Slide
      key="4"
      onNext={onClick}
      question="Descriu breument el lloc en el qual has estat pensant fins ara, des dels aspectes més físics als més abstractes:"
    ></Slide>,
    <Travel onNext={onClick} setLocation={setLocation}></Travel>,
    <StreetView
      address={location.trim()}
      APIkey="AIzaSyCl33vSM61DKpACUlT1mO5ASGd2FkFOOgo"
      streetView
      zoomLevel={15}
    />,
  ];

  return slides[index];
};

function App() {
  const [currentPage, setCurrentPage] = React.useState(0);
  const [selectedPage, setSelectedPage] = React.useState(0);
  const [location, setLocation] = React.useState('');

  React.useEffect(() => {
    if (currentPage !== selectedPage) {
      setTimeout(() => setCurrentPage(selectedPage), 250);
    }
  }, [selectedPage]);

  return (
    <div className="App">
      <header
        className={`App-header ${
          selectedPage !== currentPage
            ? 'fadeout'
            : currentPage === 5
            ? 'slowfadein'
            : 'fadein'
        }`}
      >
        {getSlide(
          currentPage,
          () => setSelectedPage(currentPage + 1),
          location,
          (location) => {
            setLocation(location);
            setSelectedPage(currentPage + 1);
          }
        )}
      </header>
    </div>
  );
}

export default App;
